._02 .control {
  z-index: 1;
  position: absolute;
  background: rgba(255, 255, 255, 0.9);
}

// Legend

.legend {
  width: 250px;
  position: absolute;
  top: 75%;
  right: 15%;
  float: right;
  z-index: 10;
  font-size: 0.9em;
  border: 1px solid $blue;
  height: auto;
  cursor: pointer;
}

.legend-header {
  background-color: $blue;
  color: white;
  text-transform: uppercase;
  padding: $blh/4;
  font-weight: $semibold;
}

.legend-body {
  padding: $blh/4;
  background-color: $white;
  opacity: 0.95;
}

.legend-item {
  display: table-row;
  height: 40px;
  
  > * {
    display: table-cell;
    vertical-align: middle;
  }
}

.legend-text {
  width: 190px;
  height: inherit;
  font-size: 0.9em;
}

.legend-illustration {
  width: 60px;
  height: inherit;
}

/* tooltip */

.tip {
  visibility: hidden;
}

.tooltip {
  position: absolute;
  font-family: $basefont;
  font-size: 12px;
  width: 120px;
  border: 1px solid $clr1;
  background-color: $clr5;  
  opacity: 0;
  padding: $blh/2;
  cursor: pointer;
  animation: tooltip 0.4s ease-in forwards;
  bottom: auto;
  right: auto;
}

#itineraries .tooltip {
  transform: translate (0,100px);
  z-index: 10;
}



@keyframes tooltip {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 0.9;
  }
}


// Search

#search {
  width: 62.5%;
  margin: 0 auto;
  padding-top: $blh;
  
  input {
    width: 100%;
    height: $blh * 2;
    font-size: $blh;
    padding: $blh/4;
    box-sizing: border-box;
  }
}

#search-results {
  margin-top: $blh;
  
  .search-result {
    padding-bottom: $blh;
    width: 650px;
  }
  
  .term {
    @include author-list-item;
    padding-bottom: 0;
  }
  
  .subhead {
    text-transform: none;
    font-size: 0.8em;
    color: darken($cool_neutral, 20%);
    font-weight: $regular;
    padding-bottom: $blh/4
  }
  
  .description {
    font-size: 0.8em;
    color: $black;
  }
}
